import React from 'react';
import { Paper, Grid, TextField, Button } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => {
    return ({
        root: {
            height: '100%',
            backgroundColor: theme.palette.background.default,
        },

    })
})

type Props = { onValidateKey: (key: string) => void }

function Key({ onValidateKey }: Props) {
    const classes = useStyles()
    const [apiKey, setApiKey] = React.useState('');

    const handleKeyChanged = (event: any) => {
        setApiKey(event.target.value)
    }
    const handleSubmit = () => {
        onValidateKey(apiKey)
    }

    return (
        <Paper square className={classes.root}>
            <Grid container spacing={2} justify="center" alignItems="center">
                <Grid item xs={5}>
                    <TextField
                        fullWidth
                        value={apiKey}
                        onChange={handleKeyChanged}
                        label="A valid OpenAI api key is needed"
                        helperText="Please enter the OpenAI api key here"
                        placeholder="OpenAI api key">
                    </TextField>
                </Grid>
                <Grid item>
                    <Button onClick={handleSubmit} color="primary" variant="contained">Submit</Button>
                </Grid>
            </Grid>
        </Paper>
    );
}

export default Key;
