import React from 'react';
import openai from 'openai-api'
import { Widget, addResponseMessage, toggleWidget, toggleInputDisabled } from 'react-chat-widget';
import { Paper, Grid, } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'

import Key from './Key'
import fetch from './fetch'

import './ChatWidgetStyles.css';

const useStyles = makeStyles(theme => {
  return ({
    root: {
      height: '100%',
      backgroundColor: theme.palette.background.default,
    },
    '@global': {
      'html': {
        backgroundColor: theme.palette.background.default,
      },
      '.rcw-header': {
        backgroundColor: theme.palette.primary.main,
        color: theme.palette.getContrastText(theme.palette.primary.main)
      },
      '.rcw-messages-container': {
        backgroundColor: theme.palette.background.paper,
        color: theme.palette.getContrastText(theme.palette.background.paper)
      },
      '.rcw-response .rcw-message-text': {
        backgroundColor: theme.palette.primary.main,
        color: theme.palette.getContrastText(theme.palette.primary.main)
      },
      '.rcw-new-message': {
        backgroundColor: theme.palette.background.paper,
        color: theme.palette.getContrastText(theme.palette.background.paper)
      },
      '.rcw-sender': {
        backgroundColor: theme.palette.background.paper,
      },
      '.rcw-send': {
        backgroundColor: theme.palette.background.paper,
      },
      '.rcw-conversation-container .rcw-close-button': {
        display: 'none'
      },
      '::-webkit-scrollbar': {
        width: '12px'
      },
      '::-webkit-scrollbar-track': {
        boxShadow: `inset 0 0 6px ${theme.palette.primary.main}`,
        '-webkit-box-shadow': `inset 0 0 6px ${theme.palette.primary.main}`,
        borderRadius: '10px',
      },
      '::-webkit-scrollbar-thumb': {
        borderRadius: '10px',
        boxShadow: `inset 0 0 6px ${theme.palette.primary.dark}`,
        '-webkit-box-shadow': `inset 0 0 6px ${theme.palette.primary.dark}`,
      }
    }
  })
})

const getLastMessage = (history: string): string => {
  const index = history.lastIndexOf("Bruno Pommier:")
  return history.substring(index)
}

function Chat() {
  const classes = useStyles()
  const [openaiWithKey, setOpenaiWithKey] = React.useState(null);
  const [launcherOpen, setLauncherOpen] = React.useState(false);
  const [history, setHistory] = React.useState('');
  const handleNewUserMessage = (message: string) => {
    toggleInputDisabled();
    fetch({
      history,
      message, openaiWithKey
    }
    ).then(newHistory => {
      if (history === 'error') {
        setOpenaiWithKey(null)
        return
      }
      setHistory(newHistory);
      toggleInputDisabled();
      addResponseMessage(getLastMessage(newHistory))
    });
  }

  React.useEffect(() => {
    if (!launcherOpen) {
      toggleWidget()
      setLauncherOpen(true)
    }
  })
  const handleValidateKey = (key: string) => {
    setOpenaiWithKey(new openai(key))
  }

  if (openaiWithKey === null) {
    return <Key onValidateKey={handleValidateKey} />
  }

  return (
    <Paper square className={classes.root}>
      <Grid container spacing={2} justify="center">
        <Grid container direction="column" >
          <Grid item xs={12} >
            <Widget handleNewUserMessage={handleNewUserMessage}
              title="Bruno Pommier's resume"
              subtitle="Ask me anything"
            />
          </Grid>
        </Grid>
        <Grid container item spacing={2} justify="center" alignItems="center">
          <Grid item>
            <a href="https://github.com/Nauss"><img src="GitHub-Mark-Light-32px.png" alt="GitHub" /></a>
          </Grid>
          <Grid item>
            <a href="https://twitter.com/TheNauss"><img src="2021 Twitter logo - white.png" alt="Twitter" style={{ width: '32px', }} /></a>
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  );
}

export default Chat;
